    private notUndefinedBuilder = (data, key) => {
        if (key.indexOf('.') !== false) {
            let _k = key.split('.');
            _k.forEach((k) => {
                if (typeof data != 'undefined') {
                    if (data) {
                        if (typeof data[k] != 'undefined') {
                            data = data[k];
                        } else {
                            data = null;
                        }
                    }
                }
            });
            return data;
        } else {
            if (typeof data != 'undefined') {
                if (data) {
                    if (typeof data[key] != 'undefined') {
                        return data[key];
                    }
                }
            }
        }
        return false;
    }

    notUndefined = (data, key) => {
        if (typeof key == 'string') {
            return this.notUndefinedBuilder(data, key);
        } else if (typeof key == 'object') {
            let check = true;
            if (key != null) {
                key.forEach((_k) => {
                    if (!this.notUndefinedBuilder(data, _k)) {
                        check = false;
                    }
                });
            } else {
                check = false;
            }
            return check;
        }
        return null;
    }

