    filenameByFaIcon(file_name, color = null) {
        let faIcon = null;
        const fa_icon_ext = {
            excel: ["xlsx", "xls"],
            word: ["doc", "docx"],
            powerpoint: ["pptx", "ppt"],
            pdf: ["pdf"],
            image: ["jpg", "jpeg", "webp", "png", "tiff", "svg", "gif", "avif", "apng"],
            video: ["webm", "mkv", "flv", "flv", "vob", "ogv", "ogg", "drc", "gif", "gifv", "mng", "avi", "MTS", "M2TS", "TS", "mov", "qt", "wmv", "yuv", "rm", "rmvb", "viv", "asf", "amv", "mp4", "m4p", "m4v", "mpg", "mp2", "mpeg", "mpe", "mpv", "mpg", "mpeg", "m2v", "m4v", "svi", "3gp", "3g2", "mxf", "roq", "nsv", "flv", "f4v", "f4p", "f4a", "f4b"],
            zipper: ["zip", "rar", "7z"],
            audio: ["mp3", "wav", "ogg", "flac", "aac", "wma", "m4a", "ape", "alac", "aiff", "mid", "amr", "pcm", "au", "ra", "aac", "ac3", "dts", "mka", "opus", "midi", "3ga", "webm", "caf", "m4r", "tta", "wv"],
            code: ["php", "html", "css", "js", "py", "java", "c", "cpp", "h", "hpp", "xml", "json", "sql", "rb", "swift", "kt", "pl", "go", "ts", "dart", "sh", "bat", "ps1"],
        };
        const fa_icon_ext_color = {
            excel: "#1d6f42",
            word: "#41A5EE",
            powerpoint: "#D04423",
            pdf: "#f40f02",
        };
        Object.keys(fa_icon_ext).forEach(fa_ico => {
            let types = fa_icon_ext[fa_ico];
            types.forEach(item => {
                if (file_name.indexOf(`\.${item}`) !== -1) {
                    faIcon = fa_ico;
                    if (color == null) color = fa_icon_ext_color[fa_ico] ?? color;
                    return {name: faIcon, color: color};
                }
            })
        });
        if (faIcon == null) faIcon = "lines";
        if (color == "") color = null;
        return {name: faIcon, color: color};
    }

