    changeSiteLanguage = (lang_code = null) => {
        if (lang_code == null) {
            let _local = (Storage.Local as any);
            if (typeof _local.settings != 'undefined') {
                if (typeof _local.settings.language != 'undefined') {
                    if (_local.settings.language != null) {
                        lang_code = _local.settings.language;
                    }
                }
            }
        }
        if (lang_code == null) {
            return;
        }
        window['lang'] = lang_code;
        let _lang_storage = localStorage.getItem('lang');
        if (_lang_storage != window['lang']) {
            localStorage.setItem('lang', lang_code);
            localStorage.removeItem('languages');
            localStorage.removeItem('language_variables');
            window.location.reload();
        }
    }

    setLangVar(key, value) {
        let cache_type = constants.lang_cache_type;
        let check = false;
        if (value == null) return;
        if (typeof Storage == 'undefined') return;
        if (typeof Storage[cache_type] == 'undefined') return;
        if (typeof Storage[cache_type].language_variables == 'undefined') Storage[cache_type].language_variables = {};
        if (typeof Storage[cache_type].language_variables != 'object') Storage[cache_type].language_variables = {};
        if (typeof Storage[cache_type].language_variables[key] != 'undefined') check = true;
        Storage[cache_type].language_variables[key] = value;
        if (value && check) return value;

        if (typeof Storage[cache_type].language_var_sync == 'undefined') Storage[cache_type].language_var_sync = {};
        if (typeof Storage[cache_type].language_var_sync != 'object') Storage[cache_type].language_var_sync = {};
        Storage[cache_type].language_var_sync[key] = value;
        Browser.storage[cache_type.toLowerCase()].set({'language_var_sync': JSON.stringify(Storage[cache_type].language_var_sync)}).then();
        return value;
    }

