    auth(App, forms, type = 'register', event) {
        let target = (event.target as any);
        let error = false;
        target.setAttribute('disabled', 'disabled');
        let container = target.closest(`#${type}`);
        document.querySelectorAll('.input-error').forEach(item => {
            item.classList.remove('input-error');
        });
        let validRegex = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
        if (forms.email == null) forms.email = "";
        if (forms.email.length < 3 || !forms.email.match(validRegex)) {
            if (container != null) container.querySelector(`[name="email"]`).classList.add('input-error');
            this.notify.error(this.__('Please check your e-mail address.'));
            error = true;
            target.removeAttribute('disabled');
        }
        if (type == 'register') {
            if (forms.password == null) forms.password = "";
            if (forms.password1 == null) forms.password1 = "";
            if (forms.agree == null) forms.agree = false;
            if (forms.password.length < 3) {
                if (container != null) container.querySelector(`[name="password"]`).classList.add('input-error');
                this.notify.error(this.__('Please check your password.'));
                target.removeAttribute('disabled');
                error = true;
            }
            if (forms.password1.length < 3) {
                if (container != null) container.querySelector(`[name="password1"]`).classList.add('input-error');
                this.notify.error(this.__('Please check your confirm password.'));
                target.removeAttribute('disabled');
                error = true;
            }
            if (forms.password1 != forms.password) {
                App.forms.password1 = null;
                if (container != null) container.querySelector(`[name="password1"]`).classList.add('input-error');
                this.notify.error(this.__('Your passwords do not match. Please check and try again.'));
                target.removeAttribute('disabled');
                error = true;
            }
            if (forms.agree == false) {
                if (container != null) container.querySelector(`[name="agree"]`).classList.add('input-error');
                this.notify.error(this.__('I have read and agree to terms.'));
                target.removeAttribute('disabled');
                error = true;
            }
            if (error == true) return;
            return this.apiCall('/auth/register', forms, 'POST').then(response => {
                if (typeof response['status'] != 'undefined') {
                    if (response['status'] == 'error') {
                        target.removeAttribute('disabled');
                        return;
                    }
                }
                return this.uuid(1).then(() => {
                    App.login = true;
                    target.removeAttribute('disabled');
                });
            });
        } else if (type == 'login') {
            if (forms.password == null) forms.password = "";

            if (forms.password.length < 3) {
                if (container != null) container.querySelector(`[name="password"]`).classList.add('input-error');
                this.notify.error(this.__('Please check your password.'));
                target.removeAttribute('disabled');
                error = true;
            }
            if (error == true) return;
            return this.apiCall('/auth/login', forms, 'POST').then(response => {
                if (typeof response['status'] != 'undefined') {
                    if (response['status'] == 'error') {
                        target.removeAttribute('disabled');
                        return;
                    }
                }
                if ((response as any).status == "success") {
                    return this.uuid(1).then(() => {
                        App.login = true;
                        target.removeAttribute('disabled');
                    });
                }
                return response;
            });
        } else if (type == 'recover') {
            if (error == true) return;
            return this.apiCall('/auth/reset_password', forms, 'POST').then(response => {
                if (typeof response['status'] != 'undefined') {
                    if (response['status'] == 'error') {
                        target.removeAttribute('disabled');
                        return;
                    }
                }
                if ((response as any).status == "success") {
                    App.login_type = 'login';
                    target.removeAttribute('disabled');
                    return true;
                }
                return response;
            });
        }
    }

