    ChatId(mode, custom_key, force = 0): number {
        let chat_id = null;
        let ai_chat_ids = (Storage.Local as any).ai_chat_ids ?? {};

        if (typeof ai_chat_ids != 'undefined' && force == 0) {
            if (typeof ai_chat_ids[mode] != 'undefined') {
                if (custom_key != null && custom_key != '__all') {
                    if (ai_chat_ids[mode][custom_key]) {
                        chat_id = ai_chat_ids[mode][custom_key];
                    } else if (typeof ai_chat_ids[mode] == 'number') {
                        chat_id = ai_chat_ids[mode][custom_key];
                    }
                } else {
                    chat_id = ai_chat_ids[mode];
                }
            }
        }
        if (chat_id == null) force = 1;
        if (mode && force == 1) {
            if (custom_key) {
                if (typeof ai_chat_ids[mode] == 'undefined' || typeof ai_chat_ids[mode] != 'object') ai_chat_ids[mode] = {};
                chat_id = ai_chat_ids[mode][custom_key] = this.newChatId();
            } else {
                chat_id = ai_chat_ids[mode] = this.newChatId();
            }
        }
        Browser.storage.local.set({ai_chat_ids: ai_chat_ids}).then();
        return chat_id;
    }

