import Browser from "webextension-polyfill";

export const Settings = {
    'switchbar': 'chat',
    'color_mode': 'auto',
    'ai_mode': 'local',
    'ai_key': null,
    'language': detectedLang(),
    'language_name': null,
    'language_detail': {},
    'send_cmd': 'enter',
    'sidebar_cmd': 'ctrl+y',
    'color_mode_cmd': 'ctrl+shift+l',
    'fullscreen_cmd': 'ctrl+shift+f',
    'sidebar_show_icon': true,
    'sidebar_position': 'right',
    'sidebar_space': 'overlay',
    'search': true,
    'search_ask_mode': 'always',
    'ai_model': "GPT-3.5",
    'web_access': false,
}

function detectedLang() {
    let lang = (navigator.language || ((navigator as any).userLanguage ?? 'en'));
    if (lang.indexOf('-') != -1) lang = lang.split("-")[0];
    if (lang.indexOf('_') != -1) lang = lang.split("_")[0];
    return lang;
}

Browser.storage.local.get('settings').then(result => {
    if (typeof result['settings'] == 'undefined') {
        Browser.storage.local.set({'settings': Settings}).then();
    } else {
        Object.keys(Settings).forEach(key => {
            if (typeof Settings[key] != 'undefined') {
                let setting = Settings[key];
                if (result.settings) {
                    if (typeof result.settings[key] == 'undefined') {
                        result.settings[key] = setting;
                    }
                }
            }
        });
        if (result.settings) Browser.storage.local.set({'settings': result.settings}).then();
    }
});