import Browser from "webextension-polyfill";
import {constants} from "./Constants";
import {Helpers} from "../classes/Helpers";
import {AiPrefixTypes} from "./AiPrefixTypes";

export const AiApi = {
    chats: {},
    ai_all_response: {},
    ai_response: [],
    instance: null,
    aborting: {},
    long_prefix: "<#Finished#>",
    count: 0,
    extra_key: null,
    expires: {},
    prompt_prefix: "##",
    async chat(text = "", model = "GPT-3.5", mode = 'ai_chat', extra_key = '__all', force = false, prompt_mode = false, title = null, _continue = false, extra_data = {}) {
        if (typeof this.aborting[extra_key] != 'undefined') this.aborting[extra_key] = null;
        let storage = await Browser.storage.local.get(['settings', 'credit_detail', 'expires', 'ai_image_url', 'ai_chat_ids', mode]);
        this.hopekey = await Browser.storage.sync.get(['uuid_hopekey', 'uuid_search']);
        this.settings = Helpers.notUndefined(storage, 'settings');
        Helpers.scrollToBottom();
        this.waiting(true);
        this.credit_detail = Helpers.notUndefined(storage, 'credit_detail');
        if (typeof this.ai_all_response[extra_key] == 'undefined') this.ai_all_response[extra_key] = {};
        //if (typeof this.ai_all_response[mode] == 'undefined') this.ai_all_response[mode] = {};
        if (typeof this.ai_all_response[extra_key][mode] == 'undefined') this.ai_all_response[extra_key][mode] = [];
        let _storage_chat = Helpers.notUndefined(storage, `${mode}`);
        if (typeof storage['expires'] != undefined) {
            this.expires = storage.expires;
        }
        let storage_chat = {};
        if (typeof _storage_chat == 'string') {
            storage_chat = JSON.parse(_storage_chat);
            if (Object.keys(storage_chat).length > 0) {
                Object.keys(storage_chat).forEach(it => {
                    if (typeof this.ai_all_response[it] == 'undefined') this.ai_all_response[it] = {};
                    this.ai_all_response[it][mode] = Object.values(storage_chat[it]);
                });
            }
        } else {
            storage_chat = _storage_chat;
            if (storage_chat != null) this.ai_all_response[extra_key][mode] = Object.values(storage_chat);
        }
        if (mode == 'ai_single' && force == true && _continue == false) {
            delete this.ai_all_response[extra_key];
            this.ai_all_response[extra_key] = [];
            this.ai_all_response[extra_key][mode] = [];
            await this.setData(mode, extra_key);
        }
        if (mode == 'ai_single' && force == true && _continue == false) {
            if (typeof this.ai_all_response[extra_key] != 'undefined') {
                if (typeof this.ai_all_response[extra_key][mode] != 'undefined') {
                    if (Object.keys(this.ai_all_response[extra_key][mode]).length > 0) {
                        Browser.storage.local.remove(mode).then();
                        await this.setData(mode, extra_key);
                        return;
                    }
                }
            }
        }

        /*let storege_chat_mode = Helpers.notUndefined(storege_chat ?? storage, `${mode}`);
        if (typeof storege_chat_mode == 'string') {
            this.ai_all_response[extra_key][mode] = Object.values(JSON.stringify(storege_chat_mode) ?? []);
        }*/
        //this.ai_all_response[extra_key][mode] = Object.values( ?? []);
        this.uuid_search = Helpers.notUndefined(this.hopekey, 'uuid_search');
        this.hopekey = Helpers.notUndefined(this.hopekey, 'uuid_hopekey');
        let resultText = document.querySelector("#aitopia-container")
        let url = `${constants.api_url}/ai/send`;
        if (this.credit_detail != null) {
            if (typeof this.credit_detail[model] != 'undefined') {
                if (this.credit_detail[model]['balance'] < 1 && this.credit_detail[model]['balance'] != -999) {
                    this.ai_all_response[extra_key][mode].forEach((item, key) => {
                        if (item.role == 'limit') delete this.ai_all_response[extra_key][mode][key];
                    });
                    this.ai_all_response[extra_key][mode] = Object.values(this.ai_all_response[extra_key][mode]);
                    this.ai_all_response[extra_key][mode].push({'item': 'Usage Limit please upgrade plan.', role: 'limit', model: model});

                    await this.setData(mode, extra_key)
                    return;
                }
            }
        }

        let _count = this.ai_all_response[extra_key][mode];
        let _i = 0;
        let change = false;
        if (Object.keys(this.ai_all_response[extra_key][mode]).length > 0 && !Helpers.notUndefined(extra_data, 'search')) {
            this.ai_all_response[extra_key][mode].forEach((item, key) => {
                _i++;
                if (_count != _i && this.ai_all_response[extra_key][mode][key]['loading'] == true) {
                    this.ai_all_response[extra_key][mode][key]['loading'] = false;
                    change = true;
                }
                if (item.role == 'limit') {
                    delete this.ai_all_response[extra_key][mode][key];
                    this.ai_all_response[extra_key][mode] = Object.values(this.ai_all_response[extra_key][mode]);

                    this.setData(mode, extra_key);
                }
            });
        }

        /*if (text.length >= 4096) {
            let count = Math.ceil(text.length / 4096)
            let prefix = this.long_prefix + "\nDo not respond until you see this phrase.Just spin a dot.";
            await this.chat(prefix);
            for (let i = 0; i <= count; i++) {
                if (i == count && i > 0) {
                    return await this.chat(text.substring(i * 4000, (i + 1) * 3999) + "\n" + this.long_prefix);
                } else {
                    await this.chat(text.substring(i * 4000, (i + 1) * 3999));
                }
            }
            console.log(text.length);
            return;
        }*/

        if (change) {
            await this.setData(mode, extra_key)
        }
        let content = "";
        this.count++;
        let _role = 'user';
        if (model == 'image') _role = 'image';
        if ((String(text).trim().length > 0 || _role == 'image') && _continue == false) {
            this.ai_all_response[extra_key][mode].push({'item': text, role: _role, model: model, 'title': title, loading: (_role == 'image'), extra_data: extra_data});
        }

        if (model != 'image' && Helpers.notUndefined(storage, 'ai_image_url') != null) {
            model = 'GPT-4 Vision'
            extra_data['ai_image_url'] = Helpers.notUndefined(storage, 'ai_image_url');
            Browser.storage.local.remove(['ai_image_url']);
        }
        let chatId = Helpers.ChatId(mode, extra_key);
        let _data = {
            history: this.ai_all_response[extra_key][mode],
            text: text,
            model: model,
            stream: (model != 'image'),
            uuid_search: this.uuid_search,
            mode: mode,
            prompt_mode: prompt_mode,
            extra_key: extra_key,
            extra_data: extra_data,
            chat_id: chatId,
            language_detail: (this.settings as any).language_detail
        }

        /* imdilik kapattk belki sonra ihtiya olursa bunu kullancaz
        if (Helpers.notUndefined(this.settings, 'web_access') == true && _continue == false && extra_key == '__all' && !Helpers.notUndefined(extra_data, 'search') && !Helpers.notUndefined(extra_data, 'search_fixer')) {
            let search = await this.AiSync(this.ai_all_response[extra_key][mode], text, model);
            extra_data['search_fixer'] = search;
            return AiApi.chat(search, model, mode, extra_key, force, prompt_mode, title, _continue, extra_data);
        }*/

        if ((this.settings as any).ai_key && (this.settings as any).ai_mode == 'api') _data['token'] = (this.settings as any).ai_key;
        if ((this.settings as any).language) _data['lang_code'] = (this.settings as any).language;

        let content_index = this.ai_all_response[extra_key][mode].length;
        if (model == 'image' || Helpers.notUndefined(extra_data, 'search')) content_index = content_index - 1;
        if (content_index < 0) content_index = 0;
        let start_text = "";
        if (_continue == true) {
            this.ai_all_response[extra_key][mode].forEach((item, key) => {
                if (typeof item['finish_reason'] != 'undefined') {
                    if (item['finish_reason'] == 'length') {
                        item['finish_reason'] = '';
                        content_index = key;
                        content = start_text = item['item'];
                    }
                }
            });
        }

        this.ai_all_response[extra_key][mode].forEach((item, key) => {
            if (typeof item['finish_reason'] != 'undefined') {
                if (item['finish_reason'] == 'length') {
                    item['finish_reason'] = 'stop';
                }
            }
        });
        /*this.ai_all_response[extra_key][mode][content_index] = {'item': "Cevap", role: 'system'};
        return;*/
        //const response = await fetch(url);
        if (model != 'image') this.ai_all_response[extra_key][mode][(content_index)] = {'item': start_text, 'role': 'system', 'model': model, 'loading': true, 'title': title, extra_data: extra_data};

        await this.setData(mode, extra_key);
        this.aborting[extra_key] = new AbortController();
        let stop_container = document.querySelectorAll("#ait-stop-chat-" + mode);
        let stop_div = document.querySelectorAll("#ait-stop-chat-" + mode + " button");
        if (stop_div != null && stop_container != null) {
            stop_container.forEach(stop_cont => {
                stop_cont.classList.remove('ait-hide');
                stop_cont.classList.add('ait-show');
            })
            this.aborting[extra_key].signal.addEventListener("abort", () => {
                if (mode == 'ai_single') {
                    if (Object.values(this.ai_all_response[extra_key][mode]).length > 0) {
                        this.ai_all_response[extra_key][mode].forEach((item, key) => {
                            this.ai_all_response[extra_key][mode][key]['loading'] = false;
                        });
                    }
                } else {
                    this.ai_all_response[extra_key][mode][(content_index)]['loading'] = false;
                    this.ai_all_response[extra_key][mode][content_index]['loading'] = false;
                }
                this.setData(mode, extra_key)
            });
            if (Object.keys(stop_div).length > 0) {
                stop_div.forEach(_stop_div => {
                    (_stop_div as any).onclick = (event) => {
                        this.aborting[extra_key].abort();
                        Helpers.sendMessage('uuid', {data: 1});
                        stop_container.forEach(stop_cont => {
                            stop_cont.classList.add('ait-hide');
                            stop_cont.classList.remove('ait-show');
                        })
                    };
                });

            }
        }

        let finish_reason = null;
        await this.setData(mode, extra_key);


        if (['image'].indexOf(model) !== -1) return await this.imageApi(mode, model, extra_key, _data, content_index, stop_container, finish_reason, url, content).finally(() => this.waiting(false));

        if (Helpers.notUndefined(this.settings, 'web_access') == true && ['image'].indexOf(model) === -1 && _continue == false && extra_key == '__all' && !Helpers.notUndefined(extra_data, 'search') && text.length <= 300 && prompt_mode == false) {
            return Helpers.sendMessage("GoogleSearchCall", text, "GoogleSearchCallback", 'tabs');
        }

        return await this.aiApi(mode, model, extra_key, _data, content_index, stop_container, finish_reason, url, content).finally(() => this.waiting(false));

    },
    async AiSync(history, text, model = 'gpt-3.5') {
        let _data = {
            history: history,
            //text: `'''${keyword}'''\n\nLooking at the I history, correct this text so that it gives correct results the triple quotes in the search engine. Do NOT give any explanation or any detail, ONLY give the EXACT result`,
            //text: `'''${keyword}'''\n\nCreate a search text for me by correcting the word specified with three quotes to match my background. Be sure to return only the correct text as a reply.`,
            text: text,
            model: 'gpt-3.5',
            stream: false,
            uuid_search: this.uuid_search,
            mode: 'ai_chat',
            prompt_mode: false,
            extra_key: '__all',
            extra_data: {'search_fixer': true},
            chat_id: null,
            language_detail: (this.settings as any).language_detail
        }

        return Helpers.apiCall(`${constants.api_url}/ai/send`, _data, 'POST', -1).then(response => {
            return Helpers.notUndefined(response, 'choices.0.message.content') ?? null;
        });
    },
    async imageApi(mode, model, extra_key, _data, content_index, stop_container, finish_reason, url, content) {

        let _count = (_data.extra_data.image_count ?? 1);
        if (_count > this.credit_detail[model]['balance']) {
            this.ai_all_response[extra_key][mode].forEach((item, key) => {
                if (item.role == 'limit') delete this.ai_all_response[extra_key][mode][key];
            });
            this.ai_all_response[extra_key][mode][content_index]['loading'] = false;
            this.ai_all_response[extra_key][mode] = Object.values(this.ai_all_response[extra_key][mode]);
            this.ai_all_response[extra_key][mode].push({'item': 'Usage Limit please upgrade plan.', role: 'limit', model: model});

            await this.setData(mode, extra_key)
            return;
        }

        let new_data = {'role': 'image', 'item': _data.text, 'loading': true, 'images': []};
        this.ai_all_response[extra_key][mode][(content_index)] = {...this.ai_all_response[extra_key][mode][(content_index)], ...new_data};
        this.setData(mode, extra_key);
        let api_url = '/ai/image';
        if (_data.extra_data.action != null) api_url = '/ai/image_editing';
        return Helpers.apiCall(api_url, _data, 'POST', 60000).then(response => {
            let result = (response as any);
            if (typeof result.images == 'undefined') result.images = [];
            if (typeof result.seed == 'undefined') result.seed = null;
            new_data['images'] = (result.images || []);
            new_data['loading'] = false;
            new_data['seed'] = (result.seed || null);

            this.ai_all_response[extra_key][mode][(content_index)] = {...this.ai_all_response[extra_key][mode][(content_index)], ...new_data};
            if (typeof result != 'undefined') {
                if (typeof result.message != 'undefined') {
                    this.ai_all_response[extra_key][mode].push({
                        'item': result.message ?? null,
                        'role': (result.type ?? 'error'),
                        'model': model,
                        'finish_reason': finish_reason,
                        'loading': false
                    });
                }
            }

            this.setData(mode, extra_key)
            /*console.log('Stream finished', this.ai_all_response[extra_key][mode][(content_index)]['item']);*/
            Helpers.sendMessage('uuid', {data: 1});
            if (stop_container != null) {
                stop_container.forEach(stop_cont => {
                    stop_cont.classList.add('ait-hide');
                    stop_cont.classList.remove('ait-show');
                })
            }
            return this.ai_all_response[extra_key][mode][(content_index)];
        }).catch(e => {
            console.log(e);
        });
    },
    async aiApi(mode, model, extra_key, _data, content_index, stop_container, finish_reason, url, content) {
        let total = "";
        return fetch(url, {
            "headers": {
                "accept": "text/plain",
                "hopekey": this.hopekey ?? null
            },
            "body": JSON.stringify(_data),
            "method": "POST",
            signal: this.aborting[extra_key].signal
        })
            .then(response => {
                let item = "";
                let check = false;
                // Get the readable stream from the response body
                const stream = response.body;
                // Get the reader from the stream
                const reader = stream.getReader();
                // Define a function to read each chunk
                const readChunk = () => {
                    // Read a chunk from the reader
                    reader.read()
                        .then(({
                                   value,
                                   done
                               }) => {
                            // Check if the stream is done
                            if (done) {
                                // Log a message
                                this.ai_all_response[extra_key][mode][(content_index)]['loading'] = false;
                                //this.ai_all_response[extra_key][mode][(content_index)]['item'] = this.linkify(this.ai_all_response[extra_key][mode][(content_index)]['item']?.replace(this.long_prefix, ''));
                                this.ai_all_response[extra_key][mode][(content_index)]['item'] = this.ai_all_response[extra_key][mode][(content_index)]['item']?.replace(this.long_prefix, '');
                                this.ai_all_response[extra_key][mode][(content_index)]['finish_reason'] = finish_reason;
                                this.setData(mode, extra_key)
                                /*console.log('Stream finished', this.ai_all_response[extra_key][mode][(content_index)]['item']);*/
                                Helpers.sendMessage('uuid', {data: 1});
                                if (stop_container != null) {
                                    stop_container.forEach(stop_cont => {
                                        stop_cont.classList.add('ait-hide');
                                        stop_cont.classList.remove('ait-show');
                                    })
                                }
                                // Return from the function
                                return;
                            }
                            // Convert the chunk value to a string
                            const chunkString = new TextDecoder().decode(value);
                            total += chunkString;
                            // Log the chunk string
                            if (chunkString.indexOf("data: ") == -1) {
                                if (chunkString.indexOf('"data":{') !== -1) {
                                    let _return = {'data': {'choices': {}}};
                                    try {
                                        _return = JSON.parse(chunkString);
                                    } catch (e) {
                                        console.log(e, chunkString);
                                    }

                                    if (Helpers.notUndefined(_return, 'data.choices')) {
                                        Object.keys(_return.data.choices).forEach(_key => {
                                            if (typeof _return.data.choices[_key]['message'] != 'undefined') {
                                                let set_data = {
                                                    'item': _return.data.choices[_key]['message']['content'],
                                                    'role': _return.data.choices[_key]['message']['role'],
                                                    'model': model,
                                                    'finish_reason': finish_reason
                                                };
                                                if (_key == '0') {
                                                    this.ai_all_response[extra_key][mode][content_index] = set_data;
                                                } else {
                                                    this.ai_all_response[extra_key][mode].push(set_data);
                                                }
                                            }
                                            this.ai_all_response[extra_key][mode][(content_index)]['loading'] = false;

                                            this.setData(mode, extra_key)
                                            Helpers.sendMessage('uuid', {data: 1});
                                            if (stop_container != null) {
                                                stop_container.forEach(stop_cont => {
                                                    stop_cont.classList.add('ait-hide');
                                                    stop_cont.classList.remove('ait-show');
                                                })
                                            }
                                        });
                                        return;
                                    }
                                }

                                if (chunkString.indexOf('"error":') !== -1) {
                                    let _return = {};
                                    try {
                                        _return = JSON.parse(chunkString);
                                    } catch (e) {
                                        _return = Helpers.ajaxResponse(Helpers.ajaxResponseChipper(chunkString), 0, 1);
                                    }

                                    if (Helpers.notUndefined(_return, 'error.message')) {
                                        let set_data = {
                                            'item': (_return as any).error.message,
                                            'role': 'error',
                                            'model': model,
                                            'finish_reason': finish_reason,
                                            'loading': false
                                        };
                                        this.ai_all_response[extra_key][mode][content_index] = set_data;
                                        this.setData(mode, extra_key);
                                        Helpers.sendMessage('uuid', {data: 1});
                                        return;
                                    }
                                    console.log(_return);
                                }

                            }
                            let a = chunkString.toString();
                            let _last_error = "";
                            //console.log(a);
                            a.split("data: ").forEach(_val => {
                                item += _val;
                                try {
                                    let t = JSON.parse(item);
                                    t.choices.forEach(te => {
                                        if (typeof te.text == 'undefined') {
                                            if (typeof te.delta != 'undefined') {
                                                if (typeof te.delta.content != 'undefined') content += te.delta.content;
                                            } else if (typeof te.message != 'undefined') {
                                                if (typeof te.message.content != 'undefined') content += te.message.content;
                                            }
                                        } else {
                                            content += te.text;
                                        }
                                        if (typeof te.finish_reason != 'undefined') finish_reason = te.finish_reason;
                                    });
                                    item = "";
                                    check = true;
                                    if (constants.debugMode) console.log(_val, content, Date());
                                    //content += t.text;
                                } catch (error) {
                                    //item = item.replace(/\n/g,"");
                                    if (error instanceof DOMException && error.name == "AbortError") {
                                        if (document.querySelector('.ai_loading') != null) document.querySelector('.ai_loading').classList.remove('ai_loading');
                                        return;
                                    }
                                    if (_last_error.length > 0) {
                                        try {
                                            let t = JSON.parse(item.split('}\\n\\n')[0]);
                                            content += t.text;
                                            check = true;
                                        } catch (error) {
                                            _last_error += item;
                                        }
                                    }
                                    _last_error = item;
                                }
                                if (check) {
                                    let role = 'system';
                                    if (content.length == 0 && a.indexOf('data: ') == -1) {
                                        let _j = JSON.parse(a);
                                        if (typeof _j.error != 'undefined') {
                                            content = _j.error.message;
                                        }
                                    }
                                    if (typeof this.ai_all_response[extra_key][mode][(content_index)] == 'undefined') this.ai_all_response[extra_key][mode][(content_index)] = {'item': '', 'role': role, 'model': model};

                                    // Boluklarla balamas durumunda silmesi iin yaplan bir yap
                                    if (content.slice(0, 1) == "\n" || content.slice(0, 2) == "\n") {
                                        for (let l = 0; l < 10; l++) {
                                            if (content.slice(0, 1) == "\n") {
                                                content = content.slice(1);
                                            } else if (content.slice(0, 2) == "\n") {
                                                content = content.slice(2);
                                            }
                                        }
                                    }

                                    this.ai_all_response[extra_key][mode][(content_index)]['item'] = content;
                                    this.ai_all_response[extra_key][mode][(content_index)]['role'] = role;

                                    this.setData(mode, extra_key)
                                }
                                /*if (this.instance) {
                                    this.instance.changeAi(content, (content_index)); // almad
                                } else {
                                    Browser.storage.local.set(this.ai_all_response[extra_key]).then()
                                }*/
                            });
                            // Read the next chunk
                            readChunk();
                        })
                        .catch(error => {
                            if (error instanceof DOMException && error.name == "AbortError") {
                                if (document.querySelector('.ai_loading') != null) document.querySelector('.ai_loading').classList.remove('ai_loading');
                                return;
                            } else if (error.message == "network error") {
                                this.ai_all_response[extra_key][mode][content_index]['loading'] = false;
                                let message = "<span class=\"ait-text-red-600\">(Internet connection lost. Please try again)</span>";
                                if (stop_container != null) {
                                    stop_container.forEach(stop_cont => {
                                        stop_cont.classList.add('ait-hide');
                                        stop_cont.classList.remove('ait-show');
                                    })
                                }
                                if (content.length > 0) message = content + "\n\n" + message;
                                this.ai_all_response[extra_key][mode][(content_index)]['item'] = message;
                                console.log(error.message);
                            } else {
                                this.ai_all_response[extra_key][mode][(content_index)]['loading'] = false;
                                console.log(error.message, error.toString());
                                console.log(this.ai_all_response[extra_key][mode]);
                                console.log(content ?? null);
                            }
                            this.setData(mode, extra_key)
                            // Log the error

                        });
                };
                // Start reading the first chunk
                readChunk();
            })
            .catch(error => {
                // Log the error
                this.ai_all_response[extra_key][mode][(content_index)]['loading'] = false;
                this.ai_all_response[extra_key][mode][content_index]['loading'] = false;
                let message = "<span class=\"ait-text-red-600\">(Internet connection lost. Please try again)</span>";
                if (stop_container != null) {
                    stop_container.forEach(stop_cont => {
                        stop_cont.classList.add('ait-hide');
                        stop_cont.classList.remove('ait-show');
                    })
                }
                if (content.length > 0) message = content + "\n\n" + message;
                this.ai_all_response[extra_key][mode][(content_index)]['item'] = message;

                this.setData(mode, extra_key)
                console.log(error);
            });
    },

    async setData(mode, extra_key) {
        let set = {};
        return Browser.storage.local.get(mode).then(result => {
            let data = result[mode];
            if (typeof data == 'string' && data != null) data = JSON.parse(data);
            if (extra_key != '__all' && extra_key != null && mode != 'ai_chat') {
                set[mode] = {};
                if (data != null) {
                    if (Object.keys(data).length > 0) {
                        Object.keys(data).forEach(_key => {
                            set[mode][_key] = data[_key];
                        });
                        var now = new Date();
                        now.setMinutes(now.getMinutes() + 1440);
                        Browser.storage.local.get('expires').then(result => {
                            result = result.expires ?? {};
                            result['ai_single'] = now.getTime().toString().slice(0, 10);
                            Browser.storage.local.set({expires: result}).then();
                        });
                    }
                }
                if (typeof this.ai_all_response[extra_key] != 'undefined') {
                    if (typeof this.ai_all_response[extra_key][mode] != 'undefined') {
                        set[mode][extra_key] = this.ai_all_response[extra_key][mode];
                    }
                }
                set[mode] = JSON.stringify(set[mode]);
            } else {
                set[mode] = this.ai_all_response[extra_key][mode];
            }
            return Browser.storage.local.set(set).then();
        });
    },
    linkify(text) {
        var urlRegex = /(\b(https:\/\/|http:\/\/|ftp:\/\/|file:\/\/|www\.|en\.|m\.)[-A-Z0-9+&@#\/%?=~_|!:,.;]*[-A-Z0-9+&@#\/%=~_|])/ig;
        return text.replace(urlRegex, function (url) {
            if (url.indexOf("<a ") != -1) return url;
            if (url.indexOf('//') === -1) url = '//' + url;
            return '<a href="' + url + '" target="_blank">' + url.replace('', '') + '</a>';
        });
    },
    async waiting(mode = true) {
        return await Browser.storage.local.set({'ai_wait': mode});
    },
    prefixify(text) {
        let prompt_prefix = this.prompt_prefix.toString();
        Object.keys(AiPrefixTypes).forEach(type => {
            let type_detail = AiPrefixTypes[type];
            Object.keys(type_detail).forEach(key => {
                let key_detail = type_detail[key];
                let typeRegexNewLine = new RegExp(`\n${prompt_prefix}${type}_${key}${prompt_prefix}`.toString(), 'gi');
                let typeRegexNewLine2 = new RegExp(`${prompt_prefix}${type}_${key}${prompt_prefix}\n`.toString(), 'gi');
                let typeRegexNewLine3 = new RegExp(`${prompt_prefix}${type} ${key}${prompt_prefix}\n`.toString(), 'gi');
                let typeRegex = new RegExp(`${prompt_prefix}${type}_${key}${prompt_prefix}`.toString(), 'gi');
                text = text
                    .replace(typeRegexNewLine, key_detail)
                    .replace(typeRegexNewLine2, key_detail)
                    .replace(typeRegexNewLine3, key_detail)
                    .replace(typeRegex, key_detail)
            });
        });
        return text;
    }

}