import Browser from "webextension-polyfill";
import {Helpers} from "../classes/Helpers";

export const Storage = {
    Local: {},
    Sync: {},
    LocalOld: {},
    SyncOld: {},
    constructor() {

    },
    async build(type = "local", return_type = 0) {
        return new Promise((resolve, reject) => {
            return Browser.storage[type].get(null as any).then((result) => {
                let variable_name = Helpers.firstUpperCase(type);
                Storage[variable_name] = Helpers.JsonParse(result);
                //if (instance) if (typeof instance.storage != 'undefined') instance.storage[variable_name] = Storage[variable_name];
                Object.keys(Storage[variable_name]).forEach((_item_key) => {
                    Storage[variable_name][_item_key] = Helpers.JsonParse(Storage[variable_name][_item_key]);
                    //if (instance) if (typeof instance.storage != 'undefined') instance.storage[variable_name][_item_key] = Storage[variable_name][_item_key];
                });
                if (type == 'local') {
                    return this.build('sync').then(result => resolve(Storage));
                }
                if (return_type == 1) return resolve(Storage[type]);
                return resolve(Storage);
            }).catch(e => {
                console.log(e);
            });
        });
    },
    async listener(instance) {
        return Browser.storage.onChanged.addListener((changes, namespace) => {
            Object.keys(changes).forEach(item => {
                let key = item;
                let oldValue = changes[item].oldValue
                let newValue = changes[item].newValue
                if (typeof Storage[namespace] == 'undefined') Storage[namespace] = {};
                if (typeof Storage[`${namespace}Old`] == 'undefined') Storage[`${namespace}Old`] = {};
                if (typeof newValue != 'undefined' && newValue != null) {
                    if (typeof newValue[key] != 'undefined') {
                        this[namespace][key] = Storage[namespace][key] = newValue[key];
                    } else {
                        this[namespace][key] = Storage[namespace][key] = newValue;
                    }
                } else {
                    this[namespace][key] = Storage[namespace][key] = null;
                }
                if (typeof oldValue != 'undefined' && oldValue != null) {
                    if (typeof oldValue[key] != 'undefined') {
                        this[`${namespace}Old`][key] = Storage[`${namespace}Old`][key] = oldValue[key];
                    } else {
                        this[`${namespace}Old`][key] = Storage[`${namespace}Old`][key] = oldValue;
                    }
                } else {
                    this[`${namespace}Old`][key] = Storage[`${namespace}Old`][key] = null;
                }
            });
            /*for (let [key, {newValue}] of Object.entries(changes)) {
                if (typeof Storage[namespace] == 'undefined') Storage[namespace] = {};
                if (typeof Storage[`${namespace}Old`] == 'undefined') Storage[`${namespace}Old`] = {};
                Storage[namespace][key] = newValue[key];
                Storage[`${namespace}Old`][key] = oldValue[key];
            }*/
            Object.keys(changes).forEach(item => {
                instance.storage = Storage;
                instance.changeListener(namespace, item, Storage[namespace][item], Storage[`${namespace}Old`][item]);
            })
            //if (instance.constants.debugMode) console.log(Storage[namespace], namespace, Storage[`${namespace}Old`], changes);
            return Storage;
        });
    },
    /*async Set(data, type = 'local') {
        Object.keys(data).forEach(item => {
            this.storage[Helpers.firstUpperCase(type)][item] = item[data];
        });
        return Browser.storage[type].set(data);
    }*/
}